module.exports = {
	data(){
		return{
			
		}
	},
	methods:{
		//
		 switch(url){
			uni.switchTab({
				url
			});	
		},
		//
		goUrl(url){
			uni.navigateTo({
				url,
				animationType: 'pop-in',
				animationDuration: 200
			});
		},
		//
		back(){
			let pages = getCurrentPages();
			if (pages && pages.length > 1) {
				uni.navigateBack({
					delta: 1,
				});
			} else {
				history.back();
			}
		},
		//
		tip (title='',icon='none'){
			uni.showToast({
				title,
				icon,
				duration:2000,
			});
		},
		//
		showload(){
			uni.showLoading({
				title: ''
			});
		},
		//
		hideload(){
			uni.hideLoading();
		},
	}
}